<script>
import { Autoplay, Pagination } from "swiper";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/pagination";

export default {
   data() {
      return {
         swiperOption: {
            breakpoints: {
               320: {
                  slidesPerView: 1,
                  spaceBetween: 10,
               },
               767: {
                  slidesPerView: 2,
                  spaceBetween: 50,
               },
               991: {
                  slidesPerView: 3,
                  spaceBetween: 30,
               },
            },
         },
      };
   },
   components: {
      Swiper,
      SwiperSlide,
   },
   setup() {
      return {
         modules: [Autoplay, Pagination],
      };
   },
   name: "HomeView",
};
</script>

<style lang="scss" scoped>
$main-color: #00cd9c;
$alt-color: #ecfff9;

%center {
   display: flex;
   justify-content: center;
   align-items: center;
}
.home {
   position: relative;
   top: 56px;
   .carousel-inner,
   .carousel-item img {
