         border: 2px solid $main-color;
         background-color: $alt-color;
         &:hover {
            background-color: $main-color;
            h5,
            p {
               color: white;
            }
         }
         img {
            height: 72px;
            border-radius: 50%;
            width: 72px;
         }
      }
   }
}
.part-three {
   .swiper-slide {
      user-select: none;
      transition: 0.3s;
      padding: 8px;
      flex-direction: column;
      overflow: hidden;
      .box {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 93%;
         img {
            height: 93%;
            object-fit: cover;
         }
      }
      &:hover .hiden {
         transform: translateY(215px);
         bottom: 280px;
      }
      .hiden {
         padding: 5px;
         position: absolute;
         transition: 0.3s;
         background-color: $main-color;
         transform: translateY(440px);
         left: 0;
         display: flex;
         flex-direction: column;
         align-items: center;
         justify-content: center;
         width: 101%;
         color: white;
         transform: translateY(550px);
         .btn {
            color: $main-color;
         }
      }
   }
}
