      }
   }
   .part-three {
      .slide {
         height: 55vh;
      }
      .box {
         background-color: $alt-color;
         overflow: hidden;
         transition: 0.3s;
         &:hover {
            background-color: $main-color;
         }
         .social {
            transform: translate(-50%, 0px);
            left: 50%;
            background: #000000b3;
            width: 100%;
            padding: 12px;
            color: white;
            transition: 0.3s;
            svg {
               transition: 0.3s;
               &:hover {
                  color: $main-color;
               }
            }
         }
         &:hover {
            .social {
               transform: translate(-50%, -46px) !important;
            }
         }
      }
   }
   .part-four {
      .box {
         padding: 10px;
         border: 2px solid $main-color;
         background-color: $alt-color;
         position: relative;
         &::before {
            content: "";
            position: absolute;
            left: 50%;
            bottom: -26px;
            transform: translateX(-50%);
            border: 13px solid;
            border-color: $main-color transparent transparent;
         }
         &::after {
            content: "";
            position: absolute;
            left: 50%;
            bottom: -24px;
            transform: translateX(-50%);
            border: 13px solid;
            border-color: $alt-color transparent transparent;
         }
      }
      svg {
         color: $main-color;
      }
   }
}
</style>
