<script>
import { Autoplay, Pagination } from "swiper";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/pagination";

export default {
   data() {
      return {
         swiperOption: {
            breakpoints: {
               320: {
                  slidesPerView: 1,
                  spaceBetween: 10,
               },
               767: {
                  slidesPerView: 3,
                  spaceBetween: 50,
               },
               991: {
                  slidesPerView: 5,
                  spaceBetween: 30,
               },
            },
         },
      };
   },
   components: {
      Swiper,
      SwiperSlide,
   },
   setup() {
      return {
         modules: [Autoplay, Pagination],
      };
   },
   name: "ContactUs",
   el: "contact",
   methods: {
      addActive(e) {
         e.target.classList.toggle("active");
         e.target.lastChild.classList.toggle("active");
         e.target.firstChild.firstChild.nextSibling.classList.toggle(
            "fa-angle-down"
         );
         e.target.firstChild.firstChild.nextSibling.classList.toggle(
            "fa-angle-up"
         );
      },
   },
};
</script>

