               content: "";
               top: 0%;
               left: 0;
               position: absolute;
               width: 100%;
               height: 100%;
               z-index: 3;
            }
            .q {
               position: relative;
               &::before,
               &::after {
                  position: absolute;
                  content: "";
                  width: 0;
                  height: 100%;
                  top: 0;
                  background-color: $main-color;
                  transition: 0.6s;
                  z-index: -1;
               }
               &::after {
                  right: 0;
               }
               &::before {
                  left: 0;
               }
            }
            &.active {
               .q {
                  //background-color: $main-color;
                  color: white;
                  &::after,
                  &::before {
                     width: 50%;
                  }
               }
            }
            .q,
            .a {
               padding: 7px;
               transition: 0.3s;
            }
            .a {
               display: none;
               &.active {
                  display: block;
               }
            }
         }
      }
   }
}
</style>
