import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import "bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

import "@fortawesome/fontawesome-free/css/all.css";
import "@fortawesome/fontawesome-free/js/all.js";

import "./sass/main.scss";
import "normalize.css";

createApp(App).use(store).use(router).mount("#app");

let popup = document.querySelector(".popup");
let btns = document.querySelectorAll(".check");
let forms = document.querySelectorAll(".data");

// script
document.addEventListener("click", (e) => {
   // close btn
   if (e.target.classList.contains("close")) {
      e.target.parentElement.classList.remove("active");
      popup.classList.remove("active");
   }

   // add actve
   if (e.target.classList.contains("fa-user")) {
      popup.classList.add("active");
   }

   // remove actve
   if (e.target.classList.contains("popup")) {
      e.target.classList.remove("active");
   }
   if (e.target.classList.contains("check")) {
      btns.forEach((btn) => {
         btn.classList.remove("active");
         e.target.classList.add("active");
      });
      if (
         e.target.classList.contains("check") &&
         e.target.classList.contains("Login")
      ) {
         forms.forEach((form) => {
            form.classList.remove("active");
         });
         forms.forEach((form) => {
            if (form.classList.contains("Login")) {
               form.classList.add("active");
            }
         });
      } else {
         forms.forEach((form) => {
            form.classList.remove("active");
         });
         forms.forEach((form) => {
            if (form.classList.contains("Register")) {
               form.classList.add("active");
            }
         });
      }
   }
});
